.productphone,.productipad,.productmacbook,.productimac{
  background-color: white;
  height: 550px;
  width: 550px;
  border-radius: 20px;
  box-sizing: border-box;
  box-shadow: rgb(197, 196, 196) 2px 2px 5px 5px;
  padding-left: 7%;
  margin-bottom: 5%;
  padding-top: 2%;
}
.productphone0,.productipad0,.productmacbook0,.productimac0{
  display: none;
}
